<template>
  <div class="collection-card">
    <a
      class="collection-card__link text-link"
      :href="url"
    >
      <responsive-image
        v-if="collection.image"
        class="collection-card__image"
        :image="{
          src: collection.image
        }"
        min-max="140-360"
      />

      <div class="collection-card__title">
        <span v-text="collection.title" />
        <icon-arrow-up-right />
      </div>
    </a>
  </div>
</template>

<script>

/**
 * Vue: Collection Card (collection-card)
 * -----------------------------------------------------------------------------
 * Collection Card to render collections from Algolia.
 *
 */
import ResponsiveImage from '~global/images/responsive-image'
import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'

export default {
  name: 'CollectionCard',

  components: {
    ResponsiveImage,
    IconArrowUpRight,
  },

  props: {
    collection: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {

    }
  },

  computed: {

    /**
     * Compute collection URL
     */
    url() {
      return `${cnvs.routes.root}collections/${this.collection.handle}`
    },
  },
}
</script>

